/* Battery Indicator */
:root{
    --color-battery: white
}

.EnvironmentsIndicators_batteryIndicator__2g6Ot{
    position: relative;
    width: 14px;
    height: 6px;
    border-radius: 2px;
    padding: 0.5px;
    border: 1px solid white;
    border: 1px solid var(--color-battery);
    margin-left: 4px;
}
.EnvironmentsIndicators_batteryIndicator__2g6Ot:after{
    position: absolute;
    content: ' ';
    width: 1.5px;
    height: 3.5px;
    top: calc(50% - 1.75px);
    left: 16.5px;
    border-radius: 1px;
    background-color: white;
    background-color: var(--color-battery);
}
.EnvironmentsIndicators_batteryIndicator__2g6Ot:before{
    position: absolute;

}
.EnvironmentsIndicators_level__ibG1S{
    width: 100%;
    height: 100%;
    background-color: white;
    background-color: var(--color-battery);
    border-radius: 1px;
}



